/* 顶部 */
.topbar{ height:1rem; font-weight: bold; background: #FFF;   line-height: 1rem; position: relative; text-align: center; border-bottom: 1px solid #F7F8F9; font-size: .32rem; color: #0F192E}
.topbar .back{ position: absolute; left: 0.16rem; top:0; height: 1.1rem;}
.topbar .back img{ height:60%; margin-top: 0.25rem;}
.payItem{ text-align: center; margin: 1.3rem auto 0.8rem auto; }
.payItem dt {color: #777;font-size:.41rem; margin-bottom: .2rem;}
.payItem dt span{ color: #0F192E;}
.payItem dd{ font-size: 0.63rem;}
/* page2 */
.payItem .el-divider--horizontal{width: 4.4rem; margin: 0.5rem auto;}
.payItem .title .el-divider__text{ font-size:0.25rem; color: #99A3B4; font-weight: normal; }
.payItem dd span{ font-size: 0.5rem;}
.borderbottom{ border-bottom: 5px solid #F7F8F9}
.orderBox{ overflow: hidden; }
.orderBox dl{ overflow: hidden; font-size: 0.34rem; border-bottom: 1px solid #F0F1F5;padding: 0 0.32rem; line-height: 1rem;}
.orderBox dl dt{ float: left; color: #333}
.orderBox dl dd{ float: right; color: #0F192E;}
.orderBox dl dd img{ width:0.38rem; height: 0.38rem; vertical-align: middle; margin-top: -.08rem;}
.orderBox div{ clear: both; font-size: 0.34rem;border-bottom: 1px solid #F7F8F9; }
.orderBox label{  -webkit-box-sizing: border-box;  box-sizing: border-box; width: 100%;padding: 0 0.32rem; line-height: 1.11rem; overflow: hidden;}
.orderBox label .el-radio__input{ float:right; margin-top: 0.5rem;}
.orderBox label .el-radio__label{ float: left; font-size: 0.34rem; color: #0F192E; padding-left: 0;}
.orderBox label .el-radio__label img{ float: left;  height: .44rem;margin: .34rem .24rem 0 0;}
/* 银行卡弹窗 */
.card_alert_bg{display: none;  position: fixed; width: 100%; height: 100vh; left: 0; top: 0; background: rgba(0,0,0,0.4); z-index: 1000}
.card_alert_box{ display: none; position:fixed; width:7.2rem; margin-left:-3.6rem; left: 50%; top: 1.02rem; z-index: 1002; background: #FFF; min-height:8.8rem; }
.card_alert_cont{ position: relative; height: 100%;  }
.card_alert_cont .pay_icon{ top:0;}
.card_alert_cont .bottom_line{ position: absolute; bottom:-1.23rem;}
.card_alert_cont .bottom_line img{ width: 100%;}
.card_alert_cont .card_alert_money{ height: 0.89rem; line-height:0.89rem; padding: 0.33rem 0; text-align: center; font-size: 0.63rem;}
.card_alert_cont .card_alert_money span{  font-size: 0.5rem;}
.alert_qrcode{ width: 5.77rem; margin: 0 auto;}
.alert_qrcode canvas{ width: 100%;}
.alert_qrcode img{ width: 100%;}
.card_alert_cont .close{position: absolute; bottom: -2.2rem; width:30px; height: 30px; border: 2px solid #BEC2CC; border-radius: 50%; margin-left:-15px; left: 50%;}
.card_alert_cont .close img{ margin: 4px auto; display: block;  padding: 0; height:22px; width:22px;}
.button{ width: 5.77rem; margin:0.3rem auto 0.2rem auto; display: block;height: 0.83rem;  font-size:0.3rem; }
.button.el-button--info{ background-color:#F7F8F9;border-color:#F7F8F9; color: #BEC2CC; }
.button.el-button--primary{ background-color:#FFF;border-color:#FFF; color: #2B53E8; }
.payItem .pay_to{ padding-top: 0.2rem; font-size: 0.3rem; color: #0F192E;font-family:'PingFang SC','Microsoft YaHei','Helvetica,Arial',sans-serif;}
.pay_qr{ text-align: center;  padding: .42rem 0; position: relative;}
.pay_icon{border-radius: 0 0 0.1rem 0;position: absolute; left: 0; top:-0.8rem; font-size: .28rem; color: #FFF;width: 1.65rem; height: 0.6rem; line-height: 0.6rem; text-align: center;}
.pay_icon.alipays{ background: #03A0E9; }
.pay_icon.wechat{ background: #07C863; }
.pay_icon.card{ background: #FDB94A; }
.pay_icon img{ width: 0.3rem; height: 0.3rem; vertical-align: middle; margin-top: -0.08rem;}
.pay_tips{position: absolute; right:0.32rem; top:-0.8rem; font-size: 0.32rem;height: 0.6rem; line-height: 0.6rem; color: #333333 }
#qrcode,.wechatQrcode{ width: 4.4rem; height: 4.4rem; margin: 0 auto;}
#qrcode canvas,.wechatQrcode img{ width: 100% !important;}
.pay_qr_cont{margin:0 auto; }
.pay_qr_cont p{ width: 100%;}
.pay_qr_cont img{ width: 100%;}
.time_line{ width: 5rem;  margin: 0.3rem auto; text-align: center; overflow: hidden;}
.time_line > span{ height: 0.63rem; line-height: 0.63rem; border-radius: .1rem; border: 1px solid #2B53E8;}
.time_line > span.saveQrcode{  width: 2.29rem; font-size: .24rem;  float: left;  color: #2B53E8;}
.time_line > span.countDown{ width: 2.2rem; font-size: .36rem; float: right;  color: #FFF; background:  #2B53E8;}
.time_line img{width: 0.5rem; height: 0.5rem; vertical-align: middle; margin-top: -0.08rem;}
.pay_text{ text-align: center; margin: 0.12rem 0 0 0; padding-bottom: .4rem; font-size: 0.32rem; color: #0F192E}
.card_tips{font-size:0.25rem; margin: 0.25rem auto 0 auto;    padding-bottom: 0;}
.pay_alert_bg {background:rgba(0, 0, 0, 0.5);position: fixed; width: 100%; height: 100%; top: 0; left: 0; z-index: 99; }
.pay_alert{ position: fixed; top: 48%; left: 50%; margin-left: -112px; text-align:  center; z-index: 999; width: 224px; height: 50px; background: rgba(0, 0, 0,0.6); border-radius: 6px; color: #FFF; font-size: 13px; line-height: 50px;}
.pay_alert .pay_alert_cbox{ position: relative;}
.pay_alert .pay_alert_cbox .pay_close{ position: absolute; width: 20px; height: 20px; border-radius: 50%; background: rgba(0, 0, 0,0.6);  top: -10px; right: -6px; line-height: 20px;}
.pay_alert .pay_alert_cbox .pay_close img{ width: 16px; height: 16px; padding: 2px;}
.payOk{ text-align: center; margin: 1.2rem auto 0.8rem auto; }
.payOk dt {color: #777;}
.payOk dt img { width:1.8rem; padding-bottom:10px;}
.payOk dd{ font-size: .34rem;}
.p-box{ position: relative;; width: 100%; display: block; }
.p-top{ position: absolute; width: 100%;}
.topbar{ position: fixed; top:0; left: 0; width: 100%; z-index: 999;}
.nav{ left: 0; position: relative;; width: 100%; display: block; }
.navItem{ position: absolute; width: 100%; left: 0; }
.back{ position: absolute; left: 0; z-index: 99999;}
.view-enter-active, .view-leave-active { -webkit-transition: all 0.5s; transition: all 0.5s; }
.view-enter, .view-leave-active { opacity: 0;}
.view-enter {-webkit-transform: translateX(1200px);transform: translateX(1200px); }
.view-leave-active {-webkit-transform: translateX(-1200px);transform: translateX(-1200px);}
.navigation-enter {-webkit-clip-path:circle(0 at 50% 50%);clip-path:circle(0 at 50% 50%)}
.navigation-enter-to {-webkit-clip-path:circle(75% at 50% 50%);clip-path:circle(75% at 50% 50%)}
.navigation-enter-active {-webkit-transition:-webkit-clip-path .4s cubic-bezier(.755,.05,.855,.06);transition:-webkit-clip-path .4s cubic-bezier(.755,.05,.855,.06);transition:clip-path .4s cubic-bezier(.755,.05,.855,.06);transition:clip-path .4s cubic-bezier(.755,.05,.855,.06), -webkit-clip-path .4s cubic-bezier(.755,.05,.855,.06);transition:clip-path .4s cubic-bezier(.755,.05,.855,.06),-webkit-clip-path .4s cubic-bezier(.755,.05,.855,.06)}
.navigation-leave {-webkit-clip-path:circle(75% at 50% 50%);clip-path:circle(75% at 50% 50%)}
.navigation-leave-to {-webkit-clip-path:circle(0 at 50% 50%);clip-path:circle(0 at 50% 50%)}
.navigation-leave-active {-webkit-transition:-webkit-clip-path .6s cubic-bezier(.23,1,.32,1);transition:-webkit-clip-path .6s cubic-bezier(.23,1,.32,1);transition:clip-path .6s cubic-bezier(.23,1,.32,1);transition:clip-path .6s cubic-bezier(.23,1,.32,1), -webkit-clip-path .6s cubic-bezier(.23,1,.32,1);transition:clip-path .6s cubic-bezier(.23,1,.32,1),-webkit-clip-path .6s cubic-bezier(.23,1,.32,1)}
