.el-message-box__status{ display: none;}
.el-message-box__status+.el-message-box__message{ padding-left: 0;}
.aaa.el-button--primary{background: #FFF;color: #606266;border: 1px solid #DCDFE6;}
/* 浮动支付 */
.p-top{ padding-bottom:2rem;}
.payFloatBox{ -webkit-box-shadow:0px 11px 37px #4f4f4f; box-shadow:0px 11px 37px #4f4f4f; width: 100%; position: fixed; bottom: 0rem; background: #03a0e9; border-radius: 30px 30px 0 0; z-index: 10;  left:0; }
.payFloatBox .button.el-button--primary{  color: #03a0e9; }
.payFloatBox .a{font-size: 12px; display:block; margin:0 auto 0.2rem auto; color: #FFF;}